.post {
    margin: 10px 50px;
    font-size: 16px;
    color: #b1e5e7;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 15px;
}

.post img {
    width: 50px;
    height: 50px;
    border: 2px solid #b1e5e7;
    border-radius: 50%;
    margin-right: 10px;
}

.ava {
    display: flex;
    flex-direction: column;
    border: 2px solid #b1e5e7;
    border-radius: 5px;
    padding: 5px;
    span {
        font-size: 12px;
        color: #b1e5e7;
        align-self: flex-end;
    }
}